/* Variables */
/* Shared */
.App-content-text-color {
  color: #4d4d4d;
}
.App-secondary-text-color {
  color: #888888;
}
.App-backdrop-image {
    height: 343px;
}
@media only screen and (max-width: 767px) {
  .App-backdrop-image {
    height: 156.25px;
  }
}
.App-modal-poster {
    margin: 35px 15px 30px 15px;
}
@media only screen and (max-width: 767px) {
  .App-modal-poster {
    width: 94px;
    height: 141.25px;
    margin: 10px 5px 5px 5px;
  }
}
.App-modal-backdrop-alpha {
    width: 100%;
    background-size: cover;
    background-image: -webkit-gradient(linear,left top, left bottom,from(rgba(0, 0, 0, 0.5)),color-stop(0%, rgba(0, 0, 0, 0.5)));
    background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.5) 0%,rgba(0, 0, 0, 0.5) 0%);
    background-image: -o-linear-gradient(rgba(0, 0, 0, 0.5) 0%,rgba(0, 0, 0, 0.5) 0%);
    background-image: linear-gradient(rgba(0, 0, 0, 0.5) 0%,rgba(0, 0, 0, 0.5) 0%);
}
.App-modal-content {
    font-size: 18px;
}
.App-modal-content h3:first-of-type {
        margin-bottom: 0;
    }
.App-modal-content h5:first-of-type {
        margin-top: 0;
    }
@media only screen and (max-width: 767px) {
  .App-modal-content {
    font-size: 14px;
  }
  .App-modal-content h3:first-of-type {
            margin-bottom: 0;
            font-size: 24px;
  }
  .App-modal-content h5 {
            font-size: 18px;
  }
}
.App-modal-movie-date {
    font-size: 18px;
}
@media only screen and (max-width: 767px) {
  .App-modal-movie-date {
    font-size: 14px;
  }
}
/* Materialize CSS specific */
.modal {
    width: 90%;
    max-width: 1280px;
}
.modal-content {
    padding: 0 !important;
}
.modal-content h4:first-of-type {
        display: none;
    }
.modal-container {
    width: 90%;
    margin: 10px auto;
}


