/* Variables */
/* Shared */
.App-content-text-color {
  color: #4d4d4d;
}
.App-secondary-text-color {
  color: #888888;
}
.App-poster-image {
    width: 185px;
    height: 278px;  
}
@media only screen and (max-width: 767px) {
  .App-poster-image {
    width: 94px;
    min-width: 94px;
    height: 141.25px;
  }
}
.App-card-title {
    margin: 0 0 8px 0;
    font-size: 24px;
    font-weight: 300;
}
@media only screen and (max-width: 767px) {
  .App-card-title {
    overflow: hidden;
    font-size: 18px;
    height: 19px;
  }
}
.App-card-secondary-content {
    margin: 0 0 8px 0;
}
.App-card-action {
    text-align: right;
}
@media only screen and (max-width: 767px) {
  .App-card-overview {
    display: none;
  }
}
/* Materialize CSS specific */
@media only screen and (max-width: 767px) {
  .card .card-content {
    padding: 5px;
  }
}
@media only screen and (max-width: 767px) {
  .card .card-action {
    padding: 5px;
  }
}


